


















@import '../../assets/style/fonts';

.sidebar__header {
  @apply flex flex-row items-center;
}

.sidebar__header__logo {
  @apply text-white;
  @apply bg-indigo-600;
  @apply p-3;
  @apply mr-3;
  @apply rounded-xl;
}

.sidebar__header__title {
  @include FontOutfit;
  @apply text-xl;
  @apply font-semibold;
}
